.fixed {
  position: fixed;
  top: 0;
  z-index: 1000;
  transition: 0.4s;
}
.header {
  background: rgba(18, 24, 41, 0.8) !important;
  backdrop-filter: blur(40px);
  width: 100% !important;
  height: 80px;
  text-decoration: none;
  position: relative;
 img{
  position: absolute;
  left: 55px;
  bottom: 20px;
  width: 40px;
  height: 40px;
 }

    .menuUl {
      margin: 0;
      display: flex;
justify-content: right;
padding: 20px 30px;
      li a {
        position: relative;
        display: inline-block;
        margin-left: 20px;
        letter-spacing: 0.02em;
        font-weight: 600;
        color: #a8aebf;
        transition: 0.4s;
        text-decoration: none;
        i {
          font-size: 17px;
        }
        &:hover {
          transform: scale(1.2);
        }
        &.active {
          color: #7b6ef6;
        }
        .counter {
          display: inline-block;
          position: absolute;
          top: 0;
          right: 0;
          font-size: 10px;
          transform: translate(13px, -11px);
          color: white;
          background-color: #7b6ef6;
          width: 15px;
          height: 15px;
          border-radius: 50%;
        }
      }
    }
  }

